<template>
  <el-row class="form-bg">
    <el-col :span="10" :offset="6">
      <el-form ref="form" :rules="rules" :model="forminfo" label-width="100px">
        <el-form-item label="上级目录"  prop="pid">
            <el-select v-model="forminfo.pid" placeholder="请选择">
              <el-option label="顶级目录" :value="0"></el-option>
              <el-option
                v-for="item in catelist"
                :key="item.id"
                :label="item.catename"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        <el-form-item label="分类名称" prop="catename">
          <el-input v-model="forminfo.catename"></el-input>
        </el-form-item>
        <el-form-item label="分类图片">
          <el-upload
            action="#"
            :auto-upload="false"
            list-type="picture-card"
            :limit="1"
            :file-list="fileList"
            :on-change="handleChange"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="forminfo.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">立即{{$route.params.id ? '修改':'创建'}}</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import { addCate, getCateList, editCate } from "@/request/category";
import { mapGetters,mapActions }  from "vuex"
export default {
  data() {
    return {
      forminfo: {
        pid: "",
        catename: "",
        img: "",
        status: 1, // 状态，1是正常，2是禁用
         
      },
      dialogImageUrl: '',
      dialogVisible: false,
      fileList:[],  // [{name:"",url:""}]
      rules:{
          pid: [{ required: true, message: '请选择上级分类', trigger: 'blur' }],
          catename: [{ required: true, message: '请输入分类名称', trigger: 'blur' }],
      }
    };
  },
  computed: {
    ...mapGetters({
      catelist:"category/catelist"
    })
  },
  created() {
    // 如果是修改页面，就请求当前页面的数据！
    if (this.$route.params.id) {
      this.getinfo();
    }
  },
  mounted() {
    if(!this.catelist.length){
      this.getlist();
    }
  },
  methods: {
      ...mapActions({
        getlist:"category/getlist"
      }),
      handleChange(file, fileList){
        // 将原生的文件信息放入到img字段中
        this.forminfo.img = file.raw
      },
      handleRemove(file, fileList) {
         this.fileList = [];
         this.forminfo.img = ""
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
    async getinfo() {
      this.forminfo = await getCateList(this.$route.params.id);
      if(this.forminfo.img){
        this.fileList = [{
          name:this.forminfo.catename,
          url:this.$host+this.forminfo.img
        }]
      }
    },
    submit() {
      // 验证
      // 如果文件信息和字段信息一起提交，那么需要使用FormData对象！
      console.log(this.forminfo)
      let fd = new FormData();
      for (let key in this.forminfo) {
        fd.append(key,this.forminfo[key])
      }
      this.$refs.form.validate(valid=>{
        if(valid){
          // 提交数据
          if (this.$route.params.id) {
            editCate(this.$route.params.id,fd);
          } else {
            addCate(fd);
          }
        }else{
            this.$message.warning('验证失败')
        }
      })
    },
    reset(){
      if (this.$route.params.id) {
        this.getinfo();
      }else{
        this.$refs.form.resetFields();
      }
    }
  },
  components: {},
};

// BootStrap
</script>
<style scoped>
</style>


